
import { defineComponent, ref } from "vue";
import { ICM208001SC01_RES, ICM208001SC01_BOARD_TERM_CONDITION_LANG, ICM208001SC01_RESGISTER_Body, ICM208002SC00_REQ_TermConditionCategoryCode } from "@/shared/type/ICM208001SC01.type";
import { modalController } from "@ionic/vue";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import FormatInput from "@/shared/components/FormatInput/FormatInput.vue";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue'

export default defineComponent({
    name: "icm208001sc01",

    components: {
        baseTab,
        basePan,
        FormatInput,
        ModifyCard,
        BreadCrumb,
        SelectOption,
        TextEditor
    },

    data() {
        const headers = [
            { key: "En", name: "common.english" },
            { key: "Km", name: "common.khmer" },
            { key: "Ko", name: "common.korean" },
            // { key: "Ja", name: "common.japanese" },
            // { key: "Zh", name: "common.chinese" }
        ];
        const boardTermsConditionLangDefaultValue = ref<Array<ICM208001SC01_BOARD_TERM_CONDITION_LANG>>([
            { languageCode: "common.english", title: "", contentDesc: "", contentHtml: "" },
            { languageCode: "common.khmer", title: "", contentDesc: "", contentHtml: "" },
            { languageCode: "common.korean", title: "", contentDesc: "", contentHtml: "" },
            { languageCode: "common.japanese", title: "", contentDesc: "", contentHtml: "" },
            { languageCode: "common.chinese", title: "", contentDesc: "", contentHtml: "" }
        ])
        const termsAndConditionDetail = ref<ICM208001SC01_RES>({
            boardCategoryCode: "",
            termConditionCode: "",
            boardCategoryName: "",
            displayYN: "",
            refScreenID: "",
            firstRegisterDate: "",
            firstRegisterTellerName: "",
            firstRegisterTime: "",
            firstRegisterTellerID: "",
            lastChangeDate: "",
            lastChangeTellerName: "",
            lastChangeTime: "",
            lastChangeTellerID: "",
            statusCode: ''
        })
        const modifyCard = {
            registerTellerId: '',
            registerTellerName: '',
            registerDate: '',
            registerTime: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
            deleteTellerId: '',
            deleteTellerName: '',
            deleteDate: '',
            deleteTime: '',
        }
        return {
            headers,
            modifyCard,
            // quill,
            termsandConditionCategoryCode: [] as ICM208002SC00_REQ_TermConditionCategoryCode[],
            boardTermsConditionLangDefaultValue,
            termsAndConditionDetail,
            isEdit: false as boolean,
            isDisabled: true as boolean,
            isInvalidInput: false as boolean,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        displayType() {
            return this.$route.query.type;
        },
        btnCancel(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit) {
                return true
            }
            return false;
        },
        btnDelete(): boolean {
            if (this.$route.query.type == 'edit' && !this.isEdit) {
                return true;
            }
            return false;
        },
        btnList(): boolean {
            if (this.$route.query.type == 'edit' && !this.isEdit) {
                return true;
            }
            return false;
        },
        btnEdit(): boolean {
            if (this.$route.query.type == 'edit' && this.isEdit == false) {
                return true;
            }
            return false;
        },
        btnSave(): boolean {
            if (this.$route.query.type == 'register' || this.isEdit == true) {
                return true;
            }
            return false;
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            this.requestTermAndCondtionCategoryCodeList()
            switch (this.$route.query.type) {
                case 'register':
                    this.$util.editableContent(true);
                    this.isDisabled = false;
                    break;
                default:
                    this.$util.editableContent(false);
                    this.requestTermsAndConditionDetail();
                    break;
            }
        } else {
            await this.requestTermAndCondtionCategoryCodeList();
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.$util.editableContent(false);
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const res: any = await this.$request.getRequest('ICM20800112', { termConditionCode: authDetailParsed.termConditionCode }, false)
                    this.termsAndConditionDetail = res.body;
                    this.assignValueToTab(this.convertArrayLanguageFromServerToClient(JSON.parse(JSON.stringify(res.body.boardTermsConditionLang))))
                } else {
                    this.termsAndConditionDetail = authDetailParsed;
                    this.assignValueToTab(this.convertArrayLanguageFromServerToClient(JSON.parse(JSON.stringify(authDetailParsed.boardTermsConditionLang))))
                }
            })
        }
    },

    watch: {
        boardTermsConditionLangDefaultValue: {
            handler(value) {
                value.forEach((item: { contentDesc: string; contentHtml: string; }) => {
                    item.contentDesc = this.$validate.formatHtmlToString(item.contentHtml)
                })
            },
            deep: true
        }
    },

    methods: {
        convertArrayLanguageFromServerToClient(listArr: Array<ICM208001SC01_BOARD_TERM_CONDITION_LANG>): Array<ICM208001SC01_BOARD_TERM_CONDITION_LANG> {
            const array = listArr.map((item: ICM208001SC01_BOARD_TERM_CONDITION_LANG) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ''
                return item
            });
            return array
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        },

        convertArrayLanguageFromClientToServer(listsArr: Array<ICM208001SC01_BOARD_TERM_CONDITION_LANG>): Array<ICM208001SC01_BOARD_TERM_CONDITION_LANG> {
            const array = listsArr.map((item: ICM208001SC01_BOARD_TERM_CONDITION_LANG) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value")
                return item
            });
            return array
        },

        async requestTermAndCondtionCategoryCodeList() {
            await this.$request.getRequest('ICM20000111', { parentCategoryCode: '005' }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.termsandConditionCategoryCode = res.body.list
                    }
                })
        },

        async requestTermsAndConditionDetail() {
            await this.$request.getRequest('ICM20800112', { termConditionCode: this.$route.query.termConditionCode }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.termsAndConditionDetail = res.body;
                        this.assignValueToTab(this.convertArrayLanguageFromServerToClient(JSON.parse(JSON.stringify(res.body.boardTermsConditionLang))))
                        this.modifyCard = {
                            registerTellerId: res?.body.firstRegisterTellerID,
                            registerTellerName: res?.body.firstRegisterTellerName,
                            registerDate: res?.body.firstRegisterDate,
                            registerTime: res?.body.firstRegisterTime,
                            updatedTellerId: this.termsAndConditionDetail.statusCode === '09' ? '' : res?.body.lastChangeTellerID,
                            updatedTellerName: this.termsAndConditionDetail.statusCode === '09' ? '' : res?.body.lastChangeTellerName,
                            updatedDate: this.termsAndConditionDetail.statusCode === '09' ? '' : res?.body.lastChangeDate,
                            updatedTime: this.termsAndConditionDetail.statusCode === '09' ? '' : res?.body.lastChangeTime,
                            deleteTellerId: this.termsAndConditionDetail.statusCode === '09' ? res?.body.lastChangeTellerID : '',
                            deleteTellerName: this.termsAndConditionDetail.statusCode === '09' ? res?.body.lastChangeTellerName : '',
                            deleteDate: this.termsAndConditionDetail.statusCode === '09' ? res?.body.lastChangeDate : '',
                            deleteTime: this.termsAndConditionDetail.statusCode === '09' ? res?.body.lastChangeTime : '',
                        }
                    }
                });
        },

        async requestDelete() {
            await this.$request.getRequest('ICM20800141', { termConditionCode: this.termsAndConditionDetail.termConditionCode }, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$dialog.onOpen({
                            content: this.$t("message.delete_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async requestRegister(body: ICM208001SC01_RESGISTER_Body) {
            this.$router.push("ICM208001SC00")
            await this.$request.getRequest('ICM20800121', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.$dialog.onOpen({
                            content: this.$t("message.register_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        async requestEdit(body: ICM208001SC01_RESGISTER_Body) {
            await this.$request.getRequest("ICM20800131", body, false)
                .then((res: any) => {
                    if (res.header.result == true) {
                        this.isEdit = false;
                        this.isDisabled = true;
                        this.requestTermsAndConditionDetail();
                        this.$dialog.onOpen({
                            content: this.$t("message.update_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                this.requestTermsAndConditionDetail()
                                modalController.dismiss()
                            }
                        });
                    }
                });
        },

        handleSave() {
            const body: ICM208001SC01_RESGISTER_Body = {
                termConditionCode: this.termsAndConditionDetail.termConditionCode,
                refScreenID: this.termsAndConditionDetail.refScreenID,
                boardCategoryCode: this.termsAndConditionDetail.boardCategoryCode,
                displayYN: this.termsAndConditionDetail.displayYN,
                boardTermsConditionLang: this.convertArrayLanguageFromClientToServer(JSON.parse(JSON.stringify(this.boardTermsConditionLangDefaultValue))),
            };
            switch (this.$route.query.type) {
                case 'register':
                    this.requestRegister(body);
                    break;
                case 'edit':
                    this.requestEdit(body);
                    this.$util.editableContent(false)
                    break;
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'cancel':
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: 'Question',
                        callback: () => {
                            if (this.$route.query.type == 'register') {
                                this.$router.back();
                            } else {
                                this.$util.editableContent(false)
                                this.isEdit = false
                                this.isDisabled = true
                                this.requestTermsAndConditionDetail();
                            }
                            modalController.dismiss()
                        },
                    });
                    break;

                case 'delete':
                    this.$dialog.onOpen({
                        content: this.$t("message.are_you_sure_you_want_to_delete"),
                        dialogMode: 'Question',
                        callback: () => {
                            modalController.dismiss();
                            this.requestDelete()
                                .then(() => {
                                    this.$router.push("ICM208001SC00")
                                })
                        },
                    });
                    break;

                case 'list':
                    this.$router.push("ICM208001SC00");
                    break;

                case 'save':
                    var englishLangObj: any = this.boardTermsConditionLangDefaultValue.find((item) => item.languageCode == 'common.english');
                    if (!this.termsAndConditionDetail.termConditionCode || !this.termsAndConditionDetail.boardCategoryCode || !this.termsAndConditionDetail.displayYN || !englishLangObj.title.trim() || !englishLangObj.contentDesc || !this.termsAndConditionDetail.refScreenID || !this.termsAndConditionDetail.boardCategoryCode) {
                        this.isInvalidInput = true;
                        this.$util.scrollToElement('inp_error')
                    } else {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: 'Question',
                            callback: () => {
                                this.handleSave()
                                modalController.dismiss();
                                this.$util.editableContent(false)
                            },
                        });
                    }
                    break;
                case 'edit':
                    this.$util.editableContent(true);
                    this.isEdit = true;
                    this.isDisabled = false;
                    break;
            }
        },

        async assignValueToTab(contentsList) {
            for (let i = 0; i < contentsList.length; i++) {
                this.boardTermsConditionLangDefaultValue.map((item) => {
                    if (item.languageCode === contentsList[i].languageCode) {
                        item.title = contentsList[i].title;
                        item.contentDesc = contentsList[i].contentDesc;
                        item.contentHtml = contentsList[i].contentHtml;
                    } else if (item.contentHtml === '') {
                        item.contentHtml = '<p></p>'
                    }
                })
            }
        },
    },
});
